@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@400;500;600;700&family=Inter:wght@400;500;600&family=JetBrains+Mono:wght@400;500&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    :root {
        --background: 240 6% 4%;
        --foreground: 0 0% 98%;
        --card: 240 6% 9%;
        --card-foreground: 0 0% 98%;
        --popover: 240 6% 9%;
        --popover-foreground: 0 0% 98%;
        --primary: 239 84% 67%;
        --primary-foreground: 0 0% 98%;
        --secondary: 240 5% 17%;
        --secondary-foreground: 0 0% 98%;
        --muted: 240 5% 17%;
        --muted-foreground: 240 5% 65%;
        --accent: 239 84% 67%;
        --accent-foreground: 0 0% 98%;
        --destructive: 0 84% 60%;
        --destructive-foreground: 0 0% 98%;
        --border: 240 5% 17%;
        --input: 240 5% 17%;
        --ring: 239 84% 67%;
        --chart-1: 239 84% 67%;
        --chart-2: 160 84% 39%;
        --chart-3: 0 84% 60%;
        --chart-4: 43 96% 56%;
        --chart-5: 280 65% 60%;
        --radius: 0.5rem;
        --success: 160 84% 39%;
        --success-glow: rgba(16, 185, 129, 0.5);
        --primary-glow: rgba(99, 102, 241, 0.5);
    }

    .dark {
        --background: 240 6% 4%;
        --foreground: 0 0% 98%;
        --card: 240 6% 9%;
        --card-foreground: 0 0% 98%;
        --popover: 240 6% 9%;
        --popover-foreground: 0 0% 98%;
        --primary: 239 84% 67%;
        --primary-foreground: 0 0% 98%;
        --secondary: 240 5% 17%;
        --secondary-foreground: 0 0% 98%;
        --muted: 240 5% 17%;
        --muted-foreground: 240 5% 65%;
        --accent: 239 84% 67%;
        --accent-foreground: 0 0% 98%;
        --destructive: 0 62.8% 30.6%;
        --destructive-foreground: 0 0% 98%;
        --border: 240 5% 17%;
        --input: 240 5% 17%;
        --ring: 239 84% 67%;
        --chart-1: 239 84% 67%;
        --chart-2: 160 84% 39%;
        --chart-3: 0 84% 60%;
        --chart-4: 43 96% 56%;
        --chart-5: 280 65% 60%;
    }
}

@layer base {
    * {
        @apply border-border;
    }
    
    html {
        @apply dark;
    }
    
    body {
        @apply bg-background text-foreground;
        font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }
    
    h1, h2, h3, h4, h5, h6 {
        font-family: 'Outfit', sans-serif;
        letter-spacing: -0.02em;
    }
    
    .font-mono {
        font-family: 'JetBrains Mono', monospace;
    }
}

/* Custom scrollbar */
::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

::-webkit-scrollbar-track {
    background: hsl(var(--background));
}

::-webkit-scrollbar-thumb {
    background: hsl(var(--muted));
    border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
    background: hsl(var(--muted-foreground));
}

/* Glassmorphism effects */
.glass {
    backdrop-filter: blur(12px);
    background: rgba(9, 9, 11, 0.8);
}

.glass-card {
    backdrop-filter: blur(8px);
    background: rgba(24, 24, 27, 0.6);
    border: 1px solid rgba(255, 255, 255, 0.05);
}

/* Glow effects */
.glow-primary {
    box-shadow: 0 0 20px -5px var(--primary-glow);
}

.glow-success {
    box-shadow: 0 0 20px -5px var(--success-glow);
}

/* Card hover effects */
.card-hover {
    transition: transform 0.2s cubic-bezier(0.4, 0, 0.2, 1), box-shadow 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}

.card-hover:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 20px -5px rgba(99, 102, 241, 0.3);
}

/* Noise overlay */
.noise-bg {
    position: relative;
}

.noise-bg::before {
    content: '';
    position: absolute;
    inset: 0;
    background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 256 256' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.9' numOctaves='4' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noiseFilter)'/%3E%3C/svg%3E");
    opacity: 0.02;
    pointer-events: none;
    z-index: 1;
}

/* Animation utilities */
@keyframes fade-in {
    from {
        opacity: 0;
        transform: translateY(10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.animate-fade-in {
    animation: fade-in 0.3s ease-out forwards;
}

.stagger-1 { animation-delay: 0.05s; }
.stagger-2 { animation-delay: 0.1s; }
.stagger-3 { animation-delay: 0.15s; }
.stagger-4 { animation-delay: 0.2s; }

/* Recharts custom styles */
.recharts-tooltip-wrapper {
    outline: none;
}

.custom-tooltip {
    background: rgba(24, 24, 27, 0.95) !important;
    border: 1px solid hsl(var(--border)) !important;
    border-radius: 8px !important;
    padding: 12px !important;
    backdrop-filter: blur(8px);
}

/* Calendar styles */
.rdp {
    --rdp-cell-size: 40px;
    --rdp-accent-color: hsl(var(--primary));
    --rdp-background-color: hsl(var(--muted));
}

.rdp-day_selected {
    background-color: hsl(var(--primary)) !important;
}
